<template>  
	<view class="messagList-box">
		<view class="messagList">
			<template v-if="list.length > 0">
				<view class="box row" v-for="(item, index) in list" :key="index">
					<view class="pic"><image :src="item.avatar" mode="aspectFill" lazy-load="true"></image></view>
					<view class="text">
						<navigator hover-class="none" :url="'/pages/message/reply?comment_id=' + item.id + '&aid=' + aid">
							<text class="t1">{{ item.name }}</text>
							<text class="t2">{{ item.add_time }}</text>
							<text class="t3">{{ item.msg }}</text>
						</navigator>
						<view class="list-media row" v-if="item.picture_and_vedio.length > 0">
							<view class="box" v-for="(v, indexs) in item.picture_and_vedio">
								<template v-if="v.type == 1">
									<image :src="v.url" mode="aspectFill" lazy-load="true" @click="preview(item.picture_and_vedio, indexs)"></image>
								</template>
								<template v-else-if="v.type == 2">
									<video :src="v.url"></video>
								</template>
							</view>
						</view>
						<view class="ico-bar row">
							<navigator hover-class="none" class="row verCenter mr1" :url="'/pages/message/reply?comment_id=' + item.id + '&aid=' + aid">
								<text class="ico-1"></text>
								<text class="ico-bar-txt">{{ item.reply_num }}</text>
							</navigator>
							<view class="row verCenter mr2" @click="commentUserLike(item.id, index)">
								<text :class="item.is_like == 1 ? 'curr-ico' : 'ico-2'"></text>
								<text class="ico-bar-txt">{{ item.likes }}</text>
							</view>
							<view class="row verCenter">
								<!-- #ifdef MP-WEIXIN -->
								<button class="ico-3" open-type="share" id="1" :data-commentid="item.id" :data-value="item.msg"></button>
								<!-- #endif -->
								<!-- #ifdef APP-PLUS -->
								<text class="ico-3"></text>
								<!-- #endif -->
							</view>
						</view>
					</view>
				</view>
			</template>
			<template v-else>
				<view class="no-date column rowCenter">
					<view class="bg"></view>
					<text>暂无留言</text>
				</view>
			</template>
			<uni-load-more :status="more" v-if="list.length >= limit"></uni-load-more>
		</view>
		<navigator class="btn" hover-class="none" :url="'/pages/message/publish?aid=' + aid">留言</navigator>
	</view>

</template>

<script>
import { API } from '@/util/api.js';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
var a;
export default {
	data() {
		return {
			aid: '',
			list: [],
			limit: 10,
			page: 1,
			flag: false,
			activeName: ''
		};
	},
	onLoad(options) {
		this.aid = options.aid;
	},
	onShow() {
		if (a) {
			a = false;
			return;
		} else {
			//清空数据
			this.page = 1;
			this.list = [];
			this.getData();
			this.getActiveName();
		}
	},
	onPullDownRefresh() {
		this.refresh();
	},
	onReachBottom() {
		if (this.flag) return;
		this.page++;
		this.getData();
	},
	onShareAppMessage(res) {
		console.log(res);
		if (res.from === 'button') {
		}

		if (res.target.id == 1) {
			return {
				title: this.activeName,
				path: '/pages/message/share?comment_id=' + res.target.dataset.commentid
			};
		}
	},
	methods: {
		preview(arr, index) {
			a = true;
			let imgArr = [];
			if (arr) {
				for (let i = 0; i < arr.length; i++) {
					if (arr[i].type == 1) {
						imgArr.push(arr[i].url);
					}
				}
			}
			uni.previewImage({
				urls: imgArr,
				current: index
			});
		},
		refresh() {
			uni.stopPullDownRefresh();
			//清空数据
			this.page = 1;
			this.list = [];
			this.getData();
		},
		getActiveName() {
			this.request(API.getActiveName, 'GET', { aid: this.aid }).then(res => {
				if (res.code === 10000) {
					this.activeName = res.data.name;
				}
			});
		},
		commentUserLike(id, index) {
			this.request(API.commentUserLike, 'GET', { comment_id: id }).then(res => {
				if (res.code === 10000) {
					this.$set(this.list[index], `likes`, this.list[index].likes * 1 + 1); //本地更新点赞数据
					this.$set(this.list[index], `is_like`, 1);
					uni.showToast({
						title: '点赞成功'
					});
				} else {
					uni.showToast({ title: res.msg, icon: 'none' });
				}
			}, true);
		},
		getData() {
			this.more = 'loading';
			this.request(API.commentGetList, 'GET', { aid: this.aid, page: this.page, limit: this.limit }, true).then(res => {
				if (res.code === 10000) {
					if (res.data) {
						if (res.data.comment_data.length > 0) {
							this.list = this.list.concat(res.data.comment_data);
						} else {
							this.flag = true;
							this.more = 'noMore';
						}
					} else {
						this.flag = true;
						this.more = 'noMore';
					}
				}
			}, true);
		}
	},
	components: { uniLoadMore }
};
</script>

<style lang="scss">
@import '../../assets/css/message/messagList.scss';
</style>
